import axios from 'axios';
export const loader = async ({ request }) => {
  // Check if the request method is POST and the path is /api_V1/add_person
  if (request.method === 'POST' && request.url === '/api_v1/add_person') {
    try {
      // Make a POST request to the Express server
      const response = await axios.post('http://localhost:3002/api_V1/add_person', JSON.parse(request.body));

      // Return the response from the Express server
      return response.data;
    } catch (error) {
      // Handle errors and return an error response
      console.error(error);
      return { status: 'error', error: 'Failed to fetch data from server' };
    }
  }

  // Return an error response for invalid requests
  return { status: 'error', message: 'Invalid request' };
};

